.imgc .card-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
}

.imgc .card {
  display: flex;
  align-items: flex-start;
  overflow: hidden;
  text-align: left;
  padding: 20px;
  max-width: 60%;
  margin: 0 auto;
  transition: all 0.3s ease;
}

.imgc .card:nth-child(even) {
  flex-direction: row-reverse; /* Reverse the direction for even cards */
  text-align: right; /* Text alignment changes */
}

.imgc .card-image {
  width: 400px;
  height: 450px;
  object-fit: cover;
  border-radius: 4px;
  margin-right: 20px;
}

.imgc .card:nth-child(even) .card-image {
  margin-left: 20px;
  margin-right: 0;
}

.imgc .card-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.imgc .card-container h2 {
  color: #FF9800;
  font-family: "Augustine";
  font-weight: 800;
  font-size: 40px;
  margin-bottom: 10px;
}

.imgc .card-container p {
  color: #000;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 150;
  font-style: normal;
  line-height: 30px;

}

.imgc .card-container .time {
  color: #9a8158;
  font-weight: bold;
  margin-top: 10px;
}

/* Responsive Design for Smaller Screens */
@media screen and (max-width: 768px) {
  .imgc .card-container .card {
      flex-direction: column;
      align-items: center;
  }

  .imgc .card-container .card-image {
      margin-right: 0;
      margin-left: 0;
      margin-bottom: 15px;
  }

  .imgc .card-container .card-content {
      text-align: center;
  }
}
.consulting-section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  /* background-color: #f4d272;  */
  border-radius: 5px;
  margin: 50px;
  gap: 50px;
}

.consulting-content {
  width: 40%;
}

.consulting-image img {
  max-width: 90%;
  height: auto;
  border-radius: 0px;
}

.consulting-content h1 {
  line-height: 1;
  color: #FF9800;
  font-family: "Augustine";
  font-weight: 800;
  font-size: 60px;
  text-shadow: 0.5px 0.5px #040302;

}

.consulting-content p {
  color: #000;
  font-size: 18px;
  font-family: "Nunito Sans", sans-serif;
  /* font-family: "Antique Serif"; */
  font-weight: 150;
  font-style: normal;
  line-height: 30px;
  max-width: 96%;
}