/* LifeCoaching.css / ImageConsulting.css */
.life-coaching-container, .image-consulting-container {
  display: flex;
  justify-content: space-between;
  padding: 50px;
  background-color: #1A1A44; /* Dark blue background */
  color: #EABF60; /* Yellowish text */
}

.text-content {
  flex: 1;
  padding-right: 20px;
}

.text-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  
}

.text-content p {
  font-size: 1.2rem;
  line-height: 1.5;
}

.image-section {
  flex: 1;
  text-align: right;
}

.image-section img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}
