 .coaching-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 100px;
    background-color: #f4d272; /* Light yellow background */
    padding: 40px;
    margin: 50px;
    border-radius: 15px;
    color: #2c2545; /* Dark purple text */

    background-image: url("/src/components/assets/orangebg.png");
    background-size: cover;
    background-repeat: no-repeat;
    max-width: 100%;
  }
  
  .coaching-content {
    max-width: 60%;
  }
  
  .coaching-content h2 {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;

    font-family: "Augustine";
    font-weight: 800;
    font-size: 50px;
  }
  
  .coaching-content ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .coaching-content ul li {
    font-size: 1.2rem;
    margin-bottom: 15px;
  }
  
  .session-details {
    display: flex;
    align-items: center;
    margin-top: 20px;
    font-size: 1rem;
  }
  
  .session-details span {
    margin-right: 15px;
  }
  
  .whatsapp-btn {
    display: inline-block;
    background-color: #2c2545; /* Dark purple background */
    color: #fff;
    padding: 10px 15px;
    border-radius: 5px;
    text-decoration: none;
    font-size: 1rem;
    margin-left: auto;
  }
  
  .coaching-image {
    max-width: 30%;
  }
  
  .coaching-image img {
    width: 100%;
    border-radius: 10px;
  }
  .coaching-section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px;
    /* background-color: #f4d272;  */
    border-radius: 5px;
    margin: 50px;
    gap: 50px;
  }
  
  .coaching-content {
    width: 40%;
  }
  
  .coaching-image img {
    max-width: 100%;
    height: auto;
    border-radius: 0px;
  }
  
  .coaching-content h1 {
    font-size: 3rem;
    line-height: 1;
    color: #FF9800;
    font-family: "Augustine";
    font-weight: 800;
    font-size: 70px;
    text-shadow: 0.5px 0.5px #040302;

  }
  
  .coaching-content p {
    color: #000;
    font-size: 20px;
    font-family: "Nunito Sans", sans-serif;
    /* font-family: "Antique Serif"; */
    font-weight: 150;
    font-style: normal;
    line-height: 30px;
    max-width: 96%;
  }
  
  /* Still Not Sure Section */
  .still-not-sure-section {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px;
    background-color: #32335D;
    margin: 0px 150px 50px 150px;
    border-radius: 5px;
    gap: 20px;

    background-image: url("/src/components/assets/orangebg.png");
    background-size: cover;
    background-repeat: no-repeat; /* Prevent the image from repeating */
    /* width: 100%; 
    height: 100%;  */
    color: #2c2545;

  }
  
  .still-not-sure-content h2 {
    font-size: 2rem;
    font-family: "Augustine";
    font-weight: 800;
    font-size: 60px;
    margin-bottom: 20px;
  }
  
  .still-not-sure-content p {
    font-size: 1.2rem;
    margin-bottom: 20px;
    color: #f4d272;
    color: #000;
    /* width: 80%; */
  }
  
  .still-not-sure-image img {
    /* max-width: 70%; */
    width: 250px;
    height: auto;
    border-radius: 10px;
    position: relative;
    right: 0;
  }
  
  button {
    background-color: #F4E293;
    background-color: #2c2545;
    
    border: none;
    padding: 10px 20px;
    font-size: 1rem;
    color: #F4E293;
    cursor: pointer;
    border-radius: 5px;
  }
  
  button:hover {
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .coaching-section, .still-not-sure-section {
      flex-direction: column;
      text-align: center;
    }
  
    .coaching-content, .still-not-sure-content, 
    .coaching-image, .still-not-sure-image {
      width: 100%;
    }
  }

  .number {
    display: inline-block;
    font-size: 3rem;  /* Large size for the numbers */
    font-weight: bold;
    color: #FF9800;  /* Gold color for numbers */
    font-family: 'Playfair Display', serif; /* Optional font */
    /* margin-bottom: 10px; */
  }