/* Mentor Testimonials Section */
.mentor-testimonials {
    display: flex;
    justify-content: center;
    align-items: center;
    /* flex-wrap: wrap; */
    padding: 40px;
    background-color: rgb(255, 245, 225);
    color: white;
    text-align: left;
  }
  
  /* Individual Testimonial Card */
  .mentor-testimonials .testimonial {
    display: flex;
    /* justify-content: space-around; */
    align-items: center;
    flex-direction: column;
    background-color: white;
    color: #333;
    border-radius: 15px;
    padding: 20px;
    max-width: 500px;
    height: 600px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px;

  }
  
  /* Image Styling for Circle Shape */
  .mentor-testimonials .testimonial-image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin: 0 auto 15px;
  }
  
  /* Heading for Testimonials */
  .mentor-testimonials .testimonial-heading {
    text-align: center;
    color: #FF9800;
    font-family: "Augustine";
    font-weight: 800;
    font-size: 25px;
    margin-bottom: 35px;
  }
  
  /* Description Styling */
  .mentor-testimonials .testimonial-description {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 25px;
    color: #555;
  }
  
  /* Name Styling */
  .mentor-testimonials .testimonial-name {
    font-weight: bold;
    font-size: 16px;
    color: #333;    
  }
  
  /* Adjust layout for smaller screens */
  @media (max-width: 768px) {
    .mentor-testimonials {
      flex-direction: column;
      align-items: center;
    }
  
    .mentor-testimonials .testimonial {
      max-width: 90%;
    }
  }
  